import React, { useState } from 'react';
import { FaChevronDown, FaChevronRight, FaBuilding, FaChartLine, FaDollarSign, FaGift, FaArrowUp, FaInfoCircle } from 'react-icons/fa';

const CompanyFundamentals = ({ fundamentals }) => {
    const [expandedSections, setExpandedSections] = useState({
        company_info: true,
        valuation_metrics: false,
        financial_metrics: false,
        dividend_info: false,
        growth_metrics: false,
        trading_info: false
    });

    const toggleSection = (section) => {
        setExpandedSections(prev => ({
            ...prev,
            [section]: !prev[section]
        }));
    };

    const renderMetric = (label, value, format = 'text') => {
        if (value === 'N/A' || value === null || value === undefined) {
            return <span className="text-gray-400">N/A</span>;
        }

        switch (format) {
            case 'currency':
                return <span className="font-semibold text-green-600">{value}</span>;
            case 'percentage':
                const numValue = parseFloat(value);
                const color = numValue > 0 ? 'text-green-600' : numValue < 0 ? 'text-red-600' : 'text-gray-600';
                return <span className={`font-semibold ${color}`}>{value}%</span>;
            case 'ratio':
                return <span className="font-semibold text-blue-600">{value}</span>;
            default:
                return <span className="font-semibold text-gray-800">{value}</span>;
        }
    };

    const renderSection = (title, icon, data, sectionKey) => {
        if (!data || Object.keys(data).length === 0) return null;

        return (
            <div className="bg-white rounded-lg shadow-md mb-4">
                <button
                    onClick={() => toggleSection(sectionKey)}
                    className="w-full px-4 py-3 flex items-center justify-between text-left hover:bg-gray-50 transition-colors"
                >
                    <div className="flex items-center space-x-2">
                        {icon}
                        <span className="font-semibold text-gray-800">{title}</span>
                    </div>
                    {expandedSections[sectionKey] ? <FaChevronDown /> : <FaChevronRight />}
                </button>
                
                {expandedSections[sectionKey] && (
                    <div className="px-4 pb-4">
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                            {Object.entries(data).map(([key, value]) => {
                                if (key === 'error') return null;
                                
                                const label = key.split('_').map(word => 
                                    word.charAt(0).toUpperCase() + word.slice(1)
                                ).join(' ');
                                
                                let format = 'text';
                                if (key.includes('yield') || key.includes('growth') || key.includes('margins') || 
                                    key.includes('change') || key.includes('ratio')) {
                                    format = 'percentage';
                                } else if (key.includes('revenue') || key.includes('income') || key.includes('profit') || 
                                         key.includes('cash') || key.includes('debt') || key.includes('cap') || 
                                         key.includes('value') || key.includes('rate')) {
                                    format = 'currency';
                                } else if (key.includes('pe_') || key.includes('price_to_') || key.includes('enterprise_to_') || 
                                         key.includes('debt_to_') || key.includes('current_') || key.includes('quick_') || 
                                         key.includes('beta')) {
                                    format = 'ratio';
                                }
                                
                                return (
                                    <div key={key} className="flex justify-between items-center py-2 border-b border-gray-100">
                                        <span className="text-sm text-gray-600">{label}</span>
                                        {renderMetric(label, value, format)}
                                    </div>
                                );
                            })}
                        </div>
                    </div>
                )}
            </div>
        );
    };

    if (!fundamentals || fundamentals.error) {
        return (
            <div className="bg-white rounded-lg shadow-md p-6">
                <div className="flex items-center space-x-2 mb-4">
                    <FaInfoCircle className="text-blue-500" />
                    <h3 className="text-lg font-semibold text-gray-800">Company Fundamentals</h3>
                </div>
                <p className="text-gray-500">
                    {fundamentals?.error || 'Fundamental data not available for this ticker.'}
                </p>
            </div>
        );
    }

    return (
        <div className="bg-white rounded-lg shadow-md p-6">
            <div className="flex items-center space-x-2 mb-6">
                <FaBuilding className="text-blue-500" />
                <h3 className="text-lg font-semibold text-gray-800">Company Fundamentals</h3>
            </div>
            
            {renderSection('Company Information', <FaBuilding className="text-blue-500" />, fundamentals.company_info, 'company_info')}
            {renderSection('Valuation Metrics', <FaChartLine className="text-green-500" />, fundamentals.valuation_metrics, 'valuation_metrics')}
            {renderSection('Financial Metrics', <FaDollarSign className="text-purple-500" />, fundamentals.financial_metrics, 'financial_metrics')}
            {renderSection('Dividend Information', <FaGift className="text-yellow-500" />, fundamentals.dividend_info, 'dividend_info')}
            {renderSection('Growth Metrics', <FaArrowUp className="text-indigo-500" />, fundamentals.growth_metrics, 'growth_metrics')}
            {renderSection('Trading Information', <FaChartLine className="text-orange-500" />, fundamentals.trading_info, 'trading_info')}
            
            <div className="mt-4 p-3 bg-blue-50 rounded-lg">
                <div className="flex items-start space-x-2">
                    <FaInfoCircle className="text-blue-500 mt-1 flex-shrink-0" />
                                           <div className="text-sm text-blue-700">
                           <p className="font-semibold mb-1">Top 10 Key Fundamentals:</p>
                           <ul className="space-y-1 text-xs">
                               <li>• <strong>P/E Ratio:</strong> Price-to-Earnings ratio indicates valuation relative to earnings</li>
                               <li>• <strong>Market Cap:</strong> Total value of company's shares</li>
                               <li>• <strong>Revenue:</strong> Total sales and business income</li>
                               <li>• <strong>Net Income:</strong> Profit after all expenses</li>
                               <li>• <strong>Debt-to-Equity:</strong> Financial leverage ratio</li>
                               <li>• <strong>Beta:</strong> Stock volatility compared to market (1.0 = market average)</li>
                               <li>• <strong>Dividend Yield:</strong> Annual dividend as percentage of stock price</li>
                               <li>• <strong>Revenue Growth:</strong> Year-over-year sales growth</li>
                               <li>• <strong>Profit Margins:</strong> Net income as percentage of revenue</li>
                               <li>• <strong>52-Week Change:</strong> Stock performance over the past year</li>
                           </ul>
                       </div>
                </div>
            </div>
        </div>
    );
};

export default CompanyFundamentals; 