import React, { useState } from 'react';
import axios from 'axios';
import { 
  LineChart, 
  Line, 
  XAxis, 
  YAxis, 
  CartesianGrid, 
  Tooltip, 
  Legend, 
  ResponsiveContainer 
} from 'recharts';
import { 
  FaSearch, 
  FaChartLine, 
  FaNewspaper, 
  FaLightbulb, 
  FaArrowUp, 
  FaArrowDown, 
  FaMinus,
  FaSpinner,
  FaBuilding
} from 'react-icons/fa';
import PredictionTable from './components/PredictionTable';
import RecommendationCard from './components/RecommendationCard';
import SentimentAnalysis from './components/SentimentAnalysis';
import CompanyFundamentals from './components/CompanyFundamentals';
import LoadingSpinner from './components/LoadingSpinner';

function App() {
  const [ticker, setTicker] = useState('');
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleSearch = async () => {
    if (!ticker.trim()) {
      setError('Please enter a ticker symbol');
      return;
    }

    setLoading(true);
    setError('');
    setData(null);

    try {
      const response = await axios.get(`/api/predict/${ticker.trim()}`);
      
      // Check if the response has an error
      if (response.data.error) {
        setError(response.data.error);
        return;
      }
      
      // Validate the response data structure
      if (!response.data.predictions || !response.data.current_price) {
        setError('Invalid data received from server');
        return;
      }
      
      // Add timeout to prevent crashes
      setTimeout(() => {
        setData(response.data);
      }, 100);
      
    } catch (err) {
      console.error('API Error:', err);
      setError(err.response?.data?.error || 'Failed to fetch data. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleSearch();
    }
  };

  const formatChartData = (technicalData) => {
    if (!technicalData) return [];
    
    return technicalData.dates.map((date, index) => ({
      date: new Date(date).toLocaleDateString(),
      'Close Price': technicalData.close[index],
      'SMA 50': technicalData.sma_50[index],
      'SMA 200': technicalData.sma_200[index]
    }));
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <div className="flex items-center">
              <FaChartLine className="h-8 w-8 text-primary-600 mr-3" />
              <h1 className="text-2xl font-bold text-gray-900">
                Stock Prediction Dashboard
              </h1>
            </div>
            <div className="flex items-center space-x-4">
              <div className="relative">
                <input
                  type="text"
                  value={ticker}
                  onChange={(e) => setTicker(e.target.value.toUpperCase())}
                  onKeyPress={handleKeyPress}
                  placeholder="Enter ticker symbol (e.g., AAPL)"
                  className="w-64 px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                />
                <button
                  onClick={handleSearch}
                  disabled={loading}
                  className="absolute right-2 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-primary-600 disabled:opacity-50"
                >
                  {loading ? <FaSpinner className="animate-spin" /> : <FaSearch />}
                </button>
              </div>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {error && (
          <div className="mb-6 bg-red-50 border border-red-200 rounded-lg p-4">
            <div className="flex">
              <div className="flex-shrink-0">
                <FaArrowDown className="h-5 w-5 text-red-400" />
              </div>
              <div className="ml-3">
                <h3 className="text-sm font-medium text-red-800">
                  Error
                </h3>
                <div className="mt-2 text-sm text-red-700">
                  {error}
                </div>
              </div>
            </div>
          </div>
        )}

        {loading && <LoadingSpinner />}

        {data && !loading && (
          <div className="space-y-8">
            {/* Current Price and Ticker Info */}
            <div className="bg-white rounded-lg shadow p-6">
              <div className="flex items-center justify-between">
                <div>
                  <h2 className="text-3xl font-bold text-gray-900">
                    {data.ticker}
                  </h2>
                  <p className="text-lg text-gray-600">
                    Current Price: ${data.current_price}
                  </p>
                </div>
                <div className="text-right">
                  <p className="text-sm text-gray-500">Last Updated</p>
                  <p className="text-sm font-medium text-gray-900">
                    {new Date().toLocaleDateString()}
                  </p>
                </div>
              </div>
            </div>

            {/* Predictions Table */}
            <div className="bg-white rounded-lg shadow">
              <div className="px-6 py-4 border-b border-gray-200">
                <h3 className="text-lg font-medium text-gray-900">
                  Price Predictions
                </h3>
              </div>
              <div className="p-6">
                <PredictionTable 
                  predictions={data.predictions} 
                  currentPrice={data.current_price}
                />
              </div>
            </div>

            {/* Recommendation */}
            <div className="bg-white rounded-lg shadow">
              <div className="px-6 py-4 border-b border-gray-200">
                <div className="flex items-center">
                  <FaLightbulb className="h-5 w-5 text-yellow-500 mr-2" />
                  <h3 className="text-lg font-medium text-gray-900">
                    AI Recommendation
                  </h3>
                </div>
              </div>
              <div className="p-6">
                <RecommendationCard recommendation={data.recommendation} />
              </div>
            </div>

            {/* Chart */}
            <div className="bg-white rounded-lg shadow">
              <div className="px-6 py-4 border-b border-gray-200">
                <h3 className="text-lg font-medium text-gray-900">
                  Price Chart with Moving Averages
                </h3>
              </div>
              <div className="p-6">
                <div className="h-96">
                  <ResponsiveContainer width="100%" height="100%">
                    <LineChart data={formatChartData(data.technical_data)}>
                      <CartesianGrid strokeDasharray="3 3" />
                      <XAxis 
                        dataKey="date" 
                        tick={{ fontSize: 12 }}
                        angle={-45}
                        textAnchor="end"
                        height={80}
                      />
                      <YAxis tick={{ fontSize: 12 }} />
                      <Tooltip 
                        formatter={(value) => [`$${value.toFixed(2)}`, '']}
                        labelFormatter={(label) => `Date: ${label}`}
                      />
                      <Legend />
                      <Line 
                        type="monotone" 
                        dataKey="Close Price" 
                        stroke="#3b82f6" 
                        strokeWidth={2}
                        dot={false}
                      />
                      <Line 
                        type="monotone" 
                        dataKey="SMA 50" 
                        stroke="#22c55e" 
                        strokeWidth={1.5}
                        dot={false}
                      />
                      <Line 
                        type="monotone" 
                        dataKey="SMA 200" 
                        stroke="#f59e0b" 
                        strokeWidth={1.5}
                        dot={false}
                      />
                    </LineChart>
                  </ResponsiveContainer>
                </div>
              </div>
            </div>

            {/* Sentiment Analysis */}
            <div className="bg-white rounded-lg shadow">
              <div className="px-6 py-4 border-b border-gray-200">
                <div className="flex items-center">
                  <FaNewspaper className="h-5 w-5 text-blue-500 mr-2" />
                  <h3 className="text-lg font-medium text-gray-900">
                    Market Sentiment Analysis
                  </h3>
                </div>
              </div>
              <div className="p-6">
                <SentimentAnalysis sentimentData={data.sentiment} />
              </div>
            </div>

            {/* Company Fundamentals */}
            <div className="bg-white rounded-lg shadow">
              <div className="px-6 py-4 border-b border-gray-200">
                <div className="flex items-center">
                  <FaBuilding className="h-5 w-5 text-purple-500 mr-2" />
                  <h3 className="text-lg font-medium text-gray-900">
                    Company Fundamentals
                  </h3>
                </div>
              </div>
              <div className="p-6">
                <CompanyFundamentals fundamentals={data.fundamentals} />
              </div>
            </div>
          </div>
        )}

        {!data && !loading && !error && (
          <div className="text-center py-12">
            <FaChartLine className="mx-auto h-12 w-12 text-gray-400" />
            <h3 className="mt-2 text-sm font-medium text-gray-900">
              No data to display
            </h3>
            <p className="mt-1 text-sm text-gray-500">
              Enter a ticker symbol above to get started.
            </p>
          </div>
        )}
      </main>
    </div>
  );
}

export default App; 